
.fugu-accordion {
  &-item{
    margin-bottom: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid var(--gray-800);
    h4{
      font-weight: bold;
    }
    &:last-child{
      margin: 0;
      padding: 0;
      border: none;
    }
  }
  &-wrap2{
    margin-top: 75px;
    @include max(991px) {
      margin-top: 40px;
    }
    .fugu-accordion-item{
      display: flex;
      margin-bottom: 24px;
      padding-bottom: 24px;
      align-items: center;
      justify-content: space-between;
      &:last-child{
        padding: 0;
        margin: 0;
      }
      h4{
        margin: 0;
        font-weight: 700;
        font-family: $body-font;
        @include max(1199px) {
          font-size: 20px;
        }
      }
    }
    
  }
  
}


a.fugu-faq-btn {
  font-size: 16px;
  position: absolute;
  bottom: 0;
  font-weight: 700;
  color: var(--gray-800);
  transition: all 0.4s;
  text-decoration: underline;
  &:hover{
    color: var(--warning-500);
  }
  @include max(991px) {
    position: inherit;
  }
}

// accordion contact page

.nexto-accordion-one{
  &.two{
    @include max(991px) {
      margin-top: 30px;
    }
  }
  .accordion-item {
    border: none;
    background-color: transparent;
    border: 1px solid rgba(19, 17, 26, 0.5);
    border-bottom: none;
    &:first-child{
      border-radius: 10px 10px 0px 0px;
    }
    &:last-child{
      border-radius: 0px 0px 10px 10px;
      border-bottom: 1px solid rgba(19, 17, 26, 0.5);
    }
  }
  .accordion-button{
    padding: 30px;
    font-weight: 600;
    font-size: 20px;
    line-height: 1;
    font-family: $body-font;
    color: var(--gray-800);
    background-color: transparent;
    @include max(767px) {
      line-height: 28px;
      font-size: 18px;
      padding: 20px;
    }
    &:not(.collapsed){
      box-shadow: none;
    }
    &:after{
      background-repeat: no-repeat;
      background-size: 16px;
      background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fillrule='evenodd' clipRule='evenodd' d='M9.0001 0.600098C9.66284 0.600098 10.2001 1.13736 10.2001 1.8001V7.8001H16.2001C16.8628 7.8001 17.4001 8.33736 17.4001 9.0001C17.4001 9.66284 16.8628 10.2001 16.2001 10.2001H10.2001V16.2001C10.2001 16.8628 9.66284 17.4001 9.0001 17.4001C8.33736 17.4001 7.8001 16.8628 7.8001 16.2001V10.2001H1.8001C1.13736 10.2001 0.600098 9.66284 0.600098 9.0001C0.600098 8.33735 1.13736 7.8001 1.8001 7.8001L7.8001 7.8001V1.8001C7.8001 1.13736 8.33736 0.600098 9.0001 0.600098Z' fill='%23111827'/%3E%3C/svg%3E%0A");
    }
    &:not(.collapsed)::after {
        transform: rotate(-45deg);
    }
  }
  .accordion-body {
    padding: 0 50px 25px 30px;
    @include max(767px) {
      padding: 0 20px 20px 20px;
    }
  }

}


// faq page
.fugu-tab-menu{
  margin-bottom: 80px;
  @include max(991px) {
    margin-bottom: 40px;
  }
  @include max(767px) {
    margin-bottom: 20px;
  }
  .nav {
    justify-content: center;
    max-width: 674px;
    margin: 0 auto;
    @include max(767px) {
      justify-content: space-between;
    }
  }
  .nav-tabs {
    border-bottom: 2px solid rgba(19, 17, 26, 0.1);
    @include max(767px) {
      border: none;
    }
    .nav-link{
      border: none;
      font-size: 16px;
      font-weight: 700;
      padding: 0;
      padding-bottom: 10px;
      margin-right: 45px;
      color: rgba(19, 17, 26, 0.8);
      position: relative;
      @include max(767px) {
        margin:10px;
      }
        &:before{
          content: "";
          left: 0;
          opacity: 0;
          bottom: -1px;
          width: 0;
          height: 2px;
          position: absolute;
          background-color: var(--warning-500);
        }
      &:last-child{
        margin-right: 0;
      }
      &.active{
        background-color: transparent;
        color: var(--warning-500);
        &:before{
          width: 100%;
          opacity: 1;
        }
      }
    }
  }
}



.fugu--faq-section{
  background-color: var(--warning-600);;
  .fugu--default-content{
    margin-right: 20px;
    @include max(991px) {
      max-width: 500px;
    }
  }
}
.fugu--faq-section2{
  z-index: 0;
  position: relative;
  background-color: var(--gray-800);
}

.fugu--accordion-one{
  .accordion-item {
    border: none;
    background-color: transparent;
    border-bottom: 2px solid var(--gray-800);
  }
  .accordion-button{
    padding: 30px 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 1;
    font-family: $body-font;
    color: var(--gray-800);
    background-color: transparent;
    @include max(767px) {
      font-size: 20px;
      line-height: 30px;
    }
